.sidebar-option{
  display: flex;
  align-items: center;
  font-size: 12px;
  padding-left: 2px;
  cursor: pointer;
}

.sidebar-option:hover {
  background-color: var(--slack-hover);
}

.sidebar-option-icon {
  padding: 10px;
  font-size: 15px!important;
}

.sidebar-option-channel{
  padding: 10px 0;

}

.sidebar-hashtag{
  padding: 10px;
}

.sidebar-option > h3 {
  font-weight: 500;
}
