.chat-input{
  border-radius: 20px;
}

.chat-input > form {
  position: relative;
  display: flex;
  justify-content: center;
}

.chat-input > form > input {
  position: fixed;
  bottom: 30px;
  width: 60%;
  border: 1px solid grey;
  border-radius: 3px;
  padding: 20px;
}

.chat-input > form > button{
  display: none;
}
